:root {
  --bg: #f7f7f7;
  --fg: #151515;
  --muted: #666;
  --panel-bg: #ffffff;
  --border: #e6e6e6;
  --shadow: 0 1px 2px rgba(0,0,0,0.04);
}

* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body {
  margin: 0;
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji";
  color: var(--fg);
  background: var(--bg);
  line-height: 1.45;
}

button, input {
  font: inherit;
}