/* Layout */
.app {
  max-width: 1160px;
  margin: 0 auto;
  padding: 24px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  gap: 16px;
  margin-bottom: 16px;
}

.header h1 {
  margin: 0 0 4px 0;
  font-weight: 650;
  letter-spacing: -0.01em;
}

.header .hint {
  color: var(--muted);
  font-size: 13px;
}

.smallmuted {
  font-size: 12px;
  color: var(--muted);
}

/* Controls */
.controls {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 16px;
  background: var(--panel-bg);
  padding: 16px;
  border-radius: 16px;
  border: 1px solid var(--border);
  box-shadow: var(--shadow);
  margin-bottom: 20px;
}

.control label {
  display: block;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 6px;
}

.row {
  display: flex;
  gap: 8px;
  align-items: center;
}

.num {
  width: 90px;
  padding: 6px 8px;
  border: 1px solid var(--border);
  border-radius: 8px;
  text-align: right;
  background: #fff;
}

.btns { display: flex; flex-wrap: wrap; gap: 8px; margin-top: 6px; }
.btn {
  padding: 6px 10px;
  border: 1px solid var(--border);
  background: #fff;
  border-radius: 8px;
  cursor: pointer;
}
.btn:hover { background: #f1f1f1; }

.checkbox-row {
  margin-top: 6px;
  display: flex;
  gap: 8px;
  align-items: center;
  font-size: 14px;
}

/* Panels */
.panels {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 20px;
}

.panel {
  background: var(--panel-bg);
  border: 1px solid var(--border);
  border-radius: 16px;
  box-shadow: var(--shadow);
  padding: 14px;
}

.panel-title { font-size: 14px; font-weight: 600; margin-bottom: 2px; }
.panel-sub { font-size: 12px; color: var(--muted); margin-bottom: 8px; }

/* SVG plot look */
.plot {
  width: 100%;
  height: auto;
  display: block;
  color: #222;
}

.frame-border {
  fill: white;
  stroke: var(--border);
}

.inner-border {
  fill: none;
  stroke: var(--border);
}

.axis-line {
  stroke: #d6d6d6;
}

.curve {
  fill: none;
  stroke: currentColor;
  stroke-width: 2.5;
}

.grid-line {
  fill: none;
  stroke: currentColor;
  stroke-width: 1.2;
  opacity: 0.9;
}

.grid-line.v {
  opacity: 0.35;
}

.footer-hint {
  margin-top: 8px;
  font-size: 12px;
  color: var(--muted);
}
